@import url(https://fonts.googleapis.com/css2?family=Fira+Mono&family=Montserrat:wght@200&family=Oswald:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.welcome {
  font-size: 45px;
  color: rgb(255, 255, 255);
  text-align: center;
  font-family: 'Oswald', sans-serif;   
}

.description {
  font-size: 25px;
  color: rgb(255, 255, 255)!important;
  text-align: center;
  font-family: 'Fira Mono', monospace;
}

#logo {
  width: 400px;
}

.center{
  text-align: center;
  display: block;
  justify-content: center;
  align-items: center;
  margin: auto;
  width: 100%;
}

a {
  text-decoration: none;
}

a:link {
  color: rgb(0, 0, 0);
}       
a:visited {
  color: rgb(0, 0, 0);
}
a:hover {
  color: rgb(0, 0, 0);
}
a:active {
  color: rgb(0, 0, 0);
}

.pulse {
  animation: pulse 3s infinite;
  margin: 0 auto;
  display: table;
  margin-top: 50px;
  animation-direction: alternate;
  animation-name: pulse;
}
    
@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

@media screen and (max-width: 765px) {

  #logo {
    width: 300px;
}  
  .description {
      font-size: 13px;
      text-align: center;
      font-family: 'Fira Mono', monospace;
